const Site = () => import('@/components/site/Site')
const Barbearia = () => import('@/pages/site/barbearia/Barbearia')
const HomeSite = () => import('@/pages/site/home/Home')
const SiteChangePassword = () => import('@/pages/site/change-password/ChangePassword')
const SiteSchedule = () => import('@/pages/site/schedule/Schedule')
const SiteScheduleDetail = () => import('@/pages/site/schedule/Detail')

const site = [
  {
    path: '/',
    component: Site,
    name: 'home',
    meta: {
      isAdmin: false
    },
    children: [
      {
        path: '',
        component: HomeSite,
        name: 'home',
        meta: {
          isAdmin: false
        }
      },
      {
        path: 'alterar-senha',
        component: SiteChangePassword,
        name: 'site-change-password',
        meta: {
          requiresAuth: true,
          isAdmin: false
        }
      },
      { 
        path: 'meus-agendamentos',
        component: SiteSchedule,
        name: 'site-schedule',
        meta: {
          requiresAuth: true,
          isAdmin: false
        }
      },
      {
        path: 'agendamento/:id',
        component: SiteScheduleDetail,
        name: 'site-schedule-detail',
        meta: {
          requiresAuth: true,
          isAdmin: false
        }
      },
      {
        path: 'barbearia/:id/:url',
        name: 'site-barbershop',
        component: Barbearia,
        meta: {
          isAdmin: false
        }
      }
    ]
  }
]

export default site