<template>
  <v-app>
    <snackbar />
    <router-view></router-view>
  </v-app>
</template>

<script>
import Snackbar from '@/components/globais/snackbar/Snackbar'
export default {
  name: 'App',
  components: { Snackbar },
  data: () => ({
    deferredPrompt: null
  }),
  methods: {
    async dismiss () {
      this.deferredPrompt = null
    },
    async install() {
      this.deferredPrompt.prompt()
    }
  }
}
</script>
